body, #root {
  height: 100%; 
}


html, body {
  overflow: hidden;
  position: fixed;
  overflow: hidden;
  overscroll-behavior: contain;
  -webkit-overflow-scrolling: touch;
  /* In Chrome 63+, Firefox 59+ and Opera 50+  */
  /* background: inherit !important; */
  height: 100%;
  width: 100%;
}

